@import '../../styles/mixins';
@import '../../styles/colors';

.product-card {
  width: 100%;
  @include toRem(16);
  display: flex;
  flex-direction: column;
  align-items: center;
  transform-origin: center;
  padding: toEm(24);
  @include border();
  transition: transition(transform);
  background-color: $color_screen;
  color: $color_text_gray;

  & .title {
    text-align: center;
    min-height: 77px;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
  }

  &:hover {
    transform: scale(1.02);
  }

  &__image-box {
    width: 200px;
    height: 300px;

    @include media(bmd) {
      height: 162px;
    }
  }

  &__image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__description {
    font-weight: 400;
    @include toRem(16);
    line-height: 1.4;
    text-align: center;
    color: $color_secondary_text;
    margin-bottom: 24px;
    max-height: 67px;
    overflow: hidden;
  }

  &__price-box {
    width: 100%;
    @include flex(space-between);
  }

  &__price {
    width: 50%;
    font-weight: 800;
    @include toRem(24);
    line-height: 1.2;
    color: $color_gray_dark;
  }

  &__button-wrapper {
    width: 50%;
    z-index: 1;

    @include media(bmd) {
      display: flex;
      justify-content: end;
    }
  }

  & .button {
    @include media(bmd) {
      @include flex;
      font-size: 0;
      width: 48px;
      height: 48px;
      background-image: url('../../assets/icons/handbag.svg');
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}